/**
 * æµ‹è¯•HTTPSè¿æ¥
 * @param {string} url - è¦æµ‹è¯•çš„URL
 * @returns {Promise<Object>} - æµ‹è¯•ç»“æœ
 */
export const testHttpsConnection = async (url) => {
  const result = {
    success: false,
    error: null,
    details: {},
    timestamp: new Date().toISOString()
  }

  try {
    console.log('å¼€å§‹æµ‹è¯•HTTPSè¿æ¥:', url)
    
    // é¦–å…ˆæ£€æŸ¥URLæ˜¯å¦æœ‰æ•ˆ
    if (!url || !url.startsWith('http')) {
      throw new Error('æ— æ•ˆçš„URLæ ¼å¼')
    }

    // ä½¿ç”¨fetchæµ‹è¯•è¿æ¥ï¼Œæ·»åŠ è¶…æ—¶æ§åˆ¶
    const controller = new AbortController()
    const timeoutId = setTimeout(() => controller.abort(), 10000) // 10ç§’è¶…æ—¶

    const response = await fetch(url, {
      method: 'GET',
      mode: 'cors',
      cache: 'no-cache',
      signal: controller.signal,
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json'
      }
    })

    clearTimeout(timeoutId)

    result.success = response.ok
    result.details = {
      status: response.status,
      statusText: response.statusText,
      headers: Object.fromEntries(response.headers.entries()),
      url: response.url,
      redirected: response.redirected,
      type: response.type
    }

    // å°è¯•è¯»å–å“åº”å†…å®¹ï¼ˆå¦‚æœå¯èƒ½ï¼‰
    try {
      const contentType = response.headers.get('content-type')
      if (contentType && contentType.includes('application/json')) {
        const data = await response.clone().json()
        result.details.responseData = data
      }
    } catch (e) {
      // å¿½ç•¥å“åº”å†…å®¹è¯»å–é”™è¯¯
    }

    console.log('HTTPSè¿æ¥æµ‹è¯•ç»“æœ:', result)
    return result

  } catch (error) {
    // è¯¦ç»†é”™è¯¯åˆ†æ
    let errorMessage = error.message
    let errorType = 'unknown'

    if (error.name === 'AbortError') {
      errorMessage = 'è¯·æ±‚è¶…æ—¶ (10ç§’)'
      errorType = 'timeout'
    } else if (error.message.includes('Failed to fetch')) {
      errorMessage = 'ç½‘ç»œè¿æ¥å¤±è´¥ï¼Œå¯èƒ½æ˜¯æœåŠ¡å™¨ä¸å¯è¾¾æˆ–CORSé—®é¢˜'
      errorType = 'network'
    } else if (error.message.includes('Mixed Content')) {
      errorMessage = 'æ··åˆå†…å®¹é”™è¯¯ï¼šHTTPSé¡µé¢æ— æ³•åŠ è½½HTTPèµ„æº'
      errorType = 'mixed_content'
    } else if (error.message.includes('CERT')) {
      errorMessage = 'SSLè¯ä¹¦éªŒè¯å¤±è´¥'
      errorType = 'ssl_certificate'
    } else if (error.message.includes('ERR_SSL')) {
      errorMessage = 'SSLè¿æ¥é”™è¯¯'
      errorType = 'ssl_error'
    }

    result.error = errorMessage
    result.errorType = errorType
    result.details = {
      name: error.name,
      message: error.message,
      stack: error.stack,
      url: url
    }

    console.error('HTTPSè¿æ¥æµ‹è¯•å¤±è´¥:', error)
    return result
  }
}

/**
 * æµ‹è¯•APIå¥åº·æ£€æŸ¥
 * @returns {Promise<Object>} - æµ‹è¯•ç»“æœ
 */
export const testApiHealth = async () => {
  // ä½¿ç”¨å¯ç”¨çš„WeatherForecastç«¯ç‚¹ä½œä¸ºå¥åº·æ£€æŸ¥
  const apiUrl = process.env.NODE_ENV === 'production' 
    ? 'https://api.openai2025.com/WeatherForecast'
    : '/api/WeatherForecast'

  return await testHttpsConnection(apiUrl)
}

